_g.toastMsg("请勿异常访问！");
this.$router.replace({
name: "test"
});
return false;
}
let group_type = this.group_type;
let group_type_t = this.group_type_t;
let name = this.name;
let telephone = this.telephone;

let params = {
owner_order_id: this.orderInfo.id,
order_id: order_id,
ref_user_id: ref_user_id,
user_id: win_user.user_id,
activity_id: activity_id,
isGroup: group_type_t,
username: name,
phone: telephone
};
console.log(params);
_g.apiPost(
"group/readyToPay",
params,
data => {

if (data.code === 0) {
this.order_id = data.order_id;
data.activity_id=activity_id;
console.log(data);
this.pay(data);
} else {
_g.closeLoading();
_g.toastMsg("支付失败，请刷新后重试或联系客服人员");
}
},
() => {
this.show_default_all = true;
this.show_default_nopay = true;
this.show_default_wait = true;
this.show_default_complate = true;
}
);
},

async pay(data) {
this.disabled = true;
if (data.code !== 0) {
_g.closeLoading();
_g.toastMsg("支付失败，请刷新后重试或联系客服人员！");
this.disabled = false;
return false;
}
this.disabled = false;

if (data.pay_amount <= 0) {
let zeropay = await _g.apiRequest("group/zeroPay", {
user_id: win_user.user_id,
activity_id: data.activity_id,
order_id: data.order_id
});
if (zeropay.code !== 0) {
_g.closeLoading();
_g.toastMsg("支付失败，请刷新后重试！");
this.disabled = false;
return false;
} else {
_g.closeLoading();
_g.toastMsg("支付成功！");

let _this = this
setTimeout(function(){
_this.$router.go(0)
},500)
}
return false;
}else{

_g.showLoading("支付中...");
let paybodytitle = _.truncate(this.baseinfo.activity_name, {
length: 45
});
let pay_data = {
openid: win_user.openid,
appid: win_appid,
body: paybodytitle,
out_trade_no: this.order_id,
total_fee: this.activityinfo.group_amount,
notify_url: config.baseUrl + "index.php/wxpay/order_groupbuy/notify"
};
console.log(pay_data);

let jsapiResult = await _g.apiRequest("wx_pay/getjsapiparameters", pay_data);

_g.closeLoading();
if (jsapiResult.code == 0) {
let _this = this
let payResult = await _g.pay(jsapiResult.data);

if (payResult === true) {
_this.$router.go(0)
this.$router.go(0)
}
} else {
_g.floatMsg("", jsapiResult.msg);
}
}



_g.closeLoading();
_g.toastMsg("支付超时，请刷新后重试！");
console.log(data);
return false;
},

